import React, { Component } from 'react';
import logo from './logo.svg';
import {SectionsContainer, ScrollToTopOnMount, Section, Header, Footer} from 'react-fullpage';
import {Row, Container, Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, Button} from 'Reactstrap';
import './App.css';

class App extends Component {

    state = {
        response: ''
    }

    componentDidMount() {
        this.callApi()
            .then(res => this.setState({response: res.express}))
            .catch(err => console.log(err));
    }

    callApi = async () => {
        const response = await fetch('/api/hello');
        const body = await response.json();

        if (response.status !== 200) throw Error(body.message);

        return body;
    };

    render() {
        let options = {
            sectionClassName:     'section',
            anchors:              ['sectionOne', 'sectionTwo', 'sectionThree'],
            scrollBar:            false,
            navigation:           true,
            verticalAlign:        false,
            sectionPaddingTop:    '50px',
            sectionPaddingBottom: '50px',
            arrowNavigation:      true
        };
        return (
            <div>

                <Header>
                    <a href="#sectionOne">Section One</a>
                    <a href="#sectionTwo">Section Two</a>
                    <a href="#sectionThree">Section Three</a>
                </Header>
                <Footer>
                    <a href="">Dcoumentation</a>
                    <a href="">Example Source</a>
                    <a href="">About</a>
                </Footer>
                <ScrollToTopOnMount />
                <SectionsContainer className="container" {...options}>
                    <Section className="custom-section" verticalAlign="true" color="#69D2E7">Page 1</Section>
                    <Card>
                        <CardTitle>Testing Card</CardTitle>
                        <CardBody>Lorem ipusm osv...</CardBody>
                    </Card>
                    <Section color="#A7DBD8">Page 2</Section>
                    <Section color="#E0E4CC">Page 3</Section>
                </SectionsContainer>
            </div>
        );
    }
}

export default App;



